
<template>
    <div class="story-container">
        <video ref="storyVedio" class="vedio" src="https://resources.laihua.com/2017-8-31/1504110642167.mp4" 
            controls="controls" 
            autoplay="autoplay" >
        </video>
        <p>方</p>
        <p>你的过去我没有参与</p>
        <p>你的未来</p>
        <p>我奉陪到底!</p>
        <p>你在的每一天都是晴天!!!</p>
    </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  beforeRouteEnter(to, from, next) {
    if (to.path == "/story") {
      // this.$refs.storyVedio.play()
      next(vm => {
        vm.$nextTick(() => {
          let rootVedio = document.querySelector(".root-video");
          rootVedio && rootVedio.pause();
          vm.$refs.storyVedio.play();
        });
      });
    }
  }
};
</script>
<style scoped lang='less'>
.story-container {
  width: 100%;
  height: 100%;
  //   background: url("../assets/img/fyf.png");
  background-size: 100% 100%;
  background-repeat: no-repeat no-repeat;
  video {
    width: 100%;
    height: 400px;
  }
  p {
    width: 100%;
    height: 50px;
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(to right, blue, #da5380);
    -webkit-background-clip: text;
    color: transparent;
    margin-top: 10px;
    &:first-of-type {
      margin-top: 80px;
    }
  }
}
</style>




