
<template>
    <div class="index-container">
      <div class="container">
         <div class="content-index">
            <p>幸福~</p>
            <p>就是找一个温暖的人</p>
            <p>过一辈子</p>
            <p>^_^</p>
         </div>
        <video 
          ref="rootvideo"
          class="root-video"
          src="https://resources.laihua.com/2018-5-16/1526477885839.mp4" 
          controls="controls"
          loop = 'true'
          autoplay="autoplay">
      </video>
      </div>
    </div>
</template>
<script>
import slide from "@/base/slide/slide";

export default {
  data() {
    return {
      items: [
        { imgUrl: require("@/assets/img/love01.png") },
        { imgUrl: require("@/assets/img/love01.png") },
        { imgUrl: require("@/assets/img/love01.png") }
      ],
      popupVisibl: false
    };
  },
  beforeRouteEnter (to, from, next) {
    next((vm)=>{
      vm.$refs.rootvideo.play()
    })
  },
  beforeRouteLeave (to, from, next) {
    next((vm)=>{
      vm.$refs.rootvideo.pause();
    })
  }
};
</script>
<style scoped lang='less'>
.index-container {
  // background: skyblue;
  width: 100%;
  height: 100%;
  // background-image: url('../assets/img/index-bg0.png');
  .container {
    width: 100%;
    height: 100%;
    background-image: url("../assets/img/sea.png");
    background-repeat: no-repeat no-repeat;
    background-size: 100% 100%;
    filter: grayscale(0.2);
    color: #ff8eb3;
    font-size: 40px;
    word-break: break-all;
    text-indent: 1rem;
    display: flex;
    justify-content: center;
    align-items: stretch;
    h4 {
      width: 100%;
      text-align: center;
      color: #fff;
    }
    .content-index {
      width: 100%;
      height: 200px;
      text-align: center;
      color: #fff;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -60%);
      z-index: 9;
      p {
        width: 100%;
        background: linear-gradient(to right, orangered, orange);
        -webkit-background-clip: text;
        color: transparent;
        font-weight: 600;
        text-align: center;
      }
    }
    .heart-container {
      width: 350px;
      height: 350px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      background: #ff8eb3;
      border: 1px solid #ff8eb3;

      .left-heart {
        background: #ff8eb3;
        border: 1px solid #ff8eb3;
        width: 350px;
        height: 350px;
        position: absolute;
        transform: rotate(90deg);
        left: -50%;
        border-radius: 50%;
      }
      .right-heart {
        background: #ff8eb3;
        border: 1px solid #ff8eb3;
        width: 350px;
        height: 350px;
        position: absolute;
        transform: rotate(90deg);
        top: -50%;
        border-radius: 50%;
        z-index: 9;
      }
    }
  }
}
._active {
  animation: rotate 1s infinite ease;
}
@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
</style>




