
<template>
    <div class="about-conatainer">
       <video 
            src="https://resources.laihua.com/2017-11-29/1511923504886.mp4"
            controls="controls"
            autoplay="autoplay" 
            ref='aboutvideo'
        ></video>
    </div>
</template>
<script>
export default {
  data() {
    return {};
  },
   beforeRouteEnter (to, from, next) {
    next((vm)=>{
      vm.$refs.aboutvideo.play()
    })
  },
  beforeRouteLeave (to, from, next) {
    next((vm)=>{
      vm.$refs.aboutvideo.pause();
    })
  }
};
</script>
<style scoped lang = 'less'>
.about-conatainer {
  width: 100%;
  height: 100%;
  background-image: url("../assets/img/fly.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
</style>




